@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
body {
  margin: 0;
  font-family: "Oswald", Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  margin: 0;
  padding-top: 20px;
  text-align: center;
  font-family: "Oswald", Helvetica, sans-serif;
  font-size: 80px;
  -webkit-transform: skewY(-10deg);
          transform: skewY(-10deg);
  letter-spacing: 4px;
  word-spacing: -8px;
  color: tomato;
  text-shadow: -1px -1px 0 firebrick, -2px -2px 0 firebrick,
    -3px -3px 0 firebrick, -4px -4px 0 firebrick, -5px -5px 0 firebrick,
    -6px -6px 0 firebrick, -7px -7px 0 firebrick, -8px -8px 0 firebrick,
    -30px 20px 40px dimgrey;
  margin-bottom: 35px;
}

h2 {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 40px;
  font-size: 30px;
  padding-right: 100px;
  padding-left: 100px;
}

.button_hero {
  display: inline-block;
  padding: 0.35em 1.8em;
  border: 0.1em solid #fff;
  margin: 0 0.3em 0.3em 0;
  border-radius: 0.12em;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #000;
  text-align: center;
  transition: all 0.2s;
  font-size: 30px;
  width: auto;
}
.button_hero:hover {
  border: 0.1em solid tomato;
  cursor: pointer;
  color: #fff;
  background-color: tomato;
}
@media all and (max-width: 30em) {
  .button_hero {
    display: block;
    margin: 0.4em auto;
  }
}

.buttons_wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.buttons_wrapper button {
  max-height: 100px;
  width: auto;
  margin-bottom: 30px;
}

